    },

    methods:{
      bingo(){
        this.utterThis = new SpeechSynthesisUtterance('han cantado bingo');
        this.synth.speak(this.utterThis);
        this.utterThis = new SpeechSynthesisUtterance('vamos a comprobar');
        this.synth.speak(this.utterThis);
        this.pausa();
      },

      lineas(){
        this.linea = true;
        this.utterThis = new SpeechSynthesisUtterance('han cantado linea');
        this.synth.speak(this.utterThis);
        this.utterThis = new SpeechSynthesisUtterance('vamos a comprobar');
        this.synth.speak(this.utterThis);
        this.pausa();
      },
      reinicio(){
        for(let i = 0; i < 100; i++){
          this.showBall[i] = false;
        }
        this.contador_bolas = 0;
        this.fin = false;
        this.primer_numero = true;
        this.inicio = false;
        this.linea = false;
        this.utterThis = new SpeechSynthesisUtterance("Se acabo");
        this.synth.speak(this.utterThis);
      },
      pausa(){
        this.fin = !this.fin;
        if(this.fin){
          this.utterThis = new SpeechSynthesisUtterance("Seguimos");
          this.synth.speak(this.utterThis);
          this.iniciar();
        } else {
          this.utterThis = new SpeechSynthesisUtterance("hacemos una Pausa");
          this.synth.speak(this.utterThis);
        }
      },

      comenzar(){
        this.fin =true;
        this.inicio = true;
        this.contador_bolas = 0;
        this.utterThis = new SpeechSynthesisUtterance("Empezamos");
        this.synth.speak(this.utterThis);
        this.iniciar();
      },

      iniciar(){
        if(this.fin) {
          this.sleep().then(() => {
            this.bola();
          });
        }
      },

      bola() {
        if (this.fin) {
          this.contador_bolas++;
          this.no_repeat = true;
          if (!this.primer_numero) {
            this.numero = Math.round(Math.random() * 98) + 1;
            while (!this.showBall[this.numero] || this.no_repeat) {
              this.numero = Math.round(Math.random() * 98) + 1;
              if (!this.showBall[this.numero]) {
                this.showBall.splice(this.numero, 1, true);
                this.utterThis = new SpeechSynthesisUtterance(this.numero);
                this.synth.speak(this.utterThis);
                if(this.numero > 9) {
                  let decena = parseInt(this.numero/10, 10);
                  this.utterThis = new SpeechSynthesisUtterance(decena);
                  this.synth.speak(this.utterThis);
                  let unidad = this.numero % 10;
                  this.utterThis = new SpeechSynthesisUtterance(unidad);
                  this.synth.speak(this.utterThis);
                }
                this.numero_out = this.numero;
                this.no_repeat = false;
              }
            }
          }
          if (this.primer_numero) {
            this.numero = Math.round(Math.random() * 98) + 1;
            this.showBall.splice(this.numero, 1, true);
            this.numero_out = this.numero;
            this.utterThis = new SpeechSynthesisUtterance(this.numero);
            this.synth.speak(this.utterThis);
            if(this.numero > 9) {
              let decena = parseInt(this.numero/10, 10);
              this.utterThis = new SpeechSynthesisUtterance(decena);
              this.synth.speak(this.utterThis);
              let unidad = this.numero % 10;
              this.utterThis = new SpeechSynthesisUtterance(unidad);
              this.synth.speak(this.utterThis);
            }
            this.primer_numero = false;
          }
          if (this.contador_bolas >= 99){ this.reinicio();}
          this.iniciar();
        }
      }
    },
  }
</script>